@import 'settings';
@import 'foundation';
@include foundation-everything;
@import 'icons/icons'; 
@import 'typeahead';

body {color: #444;}
.mb15 {margin-bottom: 15px;}
.admin {color: red !important;}
.gray-bg {background: #F7F7F7;}
.gray {color: #999;}
.fs12 {font-size: 12px !important;}
.nowrap {white-space: nowrap;}
.mr0 {margin: 0 !important;}
.mrt0 {margin-top: 0 !important;} 
.l-header-wrap {
	margin-bottom: 25px; 
}
.l-header {
	background: #fff;
}
.l-header .logo {
	display: inline-block;
	padding: 4px 5px 4px 33px;
	margin:  5px 5px 5px 0; 
	font-size: 1.22rem;
	position: relative;
	color: #5dc9e9;
	.fi-shield {
		position: absolute;
		top: -8px;
		left: 0;
		font-size: 2.5rem;
	}
	@include hide-for-only(small)
}
.l-footer {
	border-top: 2px solid #eee;
	margin-top: 50px;
	background: #fafafa;
	padding: 24px 0;
}

.b-footer-links {
	margin: 0;
	padding: 0;
	list-style: none;
	li {
		display: inline-block;
		margin-left: 12px;
		&:first-of-type {margin-left: 0;}
		&:hover a {
			text-decoration: underline;
		}
	}
	
}
.copyright {text-align: right;color: #666;margin: 0;font-size: 0.75rem;}

.b-pfl-list {
	display: block;
	background: #f9f9f9;
	border: 1px solid #cacaca;
	border-color: transparent transparent #cacaca  transparent;
	padding: 14px;
	color: #000;
	&:hover {
		background: #f1faff;
		border-color:#c6e4f2;
		color: #000;
		margin-top: -1px;
		padding-top: 15px;
	}
	.age {
		margin-left: 7px;
		color: #999;
		small {font-weight: 100;}
		}
	.header-wrapper {
		display: block;
		color: #2199e8;
		font-size: 1.4rem;
    	font-weight: 700;
    	margin-bottom: .3rem;
	}
	.info {
		overflow: hidden;
	    display: inline-block;
		.nowrap {
			display: inline-block;
		    margin-left: -15px;
		    margin-right: 10px;
		    &:before {
		    	content: "";
			    background-color: #999;
			    width: 3px;
			    height: 3px;
			    display: inline-block;
			    position: relative;
			    vertical-align: middle;
			    width: 3px;
			    margin: 0 6px;
		    }
		}
	}
}

.b-top-search {
	background-color: rgba(93,201,233,1);
	background: linear-gradient(to bottom, rgba(93,201,233,1) 0%,rgba(1,184,228,1) 100%);
	border-bottom: 1px solid #5dc9e9;
	padding: 2rem 0 1rem;
	box-shadow: 0 1px 4px rgba(0, 0, 0, 0.2);
	.form-error {
		position: absolute;
	}
	.main-label {
		text-transform: uppercase;
		font-size: 1.30rem;
    	line-height: 2.8rem;
    	font-weight: 100;
    	color: #fff;
    	//@include breakpoint(medium down) {font-size: .8rem;font-weight: 700;}
	}
	.button {
		font-size: 1.2rem;
    	padding: 0.6em 1em;
	}
	input {border-color: #fff;box-shadow: none;}
}


.b-top-menu {
	list-style: none;
	margin: 0;
	float: none;
	font-size: 1.1rem;
	li {
		display: block;
		a {
			padding: 10px;
			border-top: 1px solid #dedede;
				display: block;
			color: #707070;
			&:hover {
				text-decoration: none;
				background: #f6f6f6;
			}
			i {
				margin-right: 5px;
			}
			@include breakpoint(medium) {
				border: 0;
				display: inline-block;
				border-left: 1px solid #dedede;
			}
		}
		&:last-child{
			a {
				margin-right: 0;
			}
		}
		@include breakpoint(medium) {
			display: inline-block;
		}
	}
	@include breakpoint(medium) {
		float: right;
	}
}



.index-box {
  border-radius: 3px;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  padding: 10px 25px;
  display: block;
  margin-top: 60px;
}

.index-box-icon {
  background-color: #569;
  border-radius: 50%;
  display: table;
  height: 100px;
  margin: 0 auto;
  width: 100px;
  margin-top: -61px;
  font-size: 3rem;
  span {
    color: #fff;
    display: table-cell;
    text-align: center;
    vertical-align: middle;
  }
}

.index-info {
  h4 {
    font-size: 26px;
    letter-spacing: 2px;
    text-transform: uppercase;
  }
  p {
    color: #717171;
    font-size: 16px;
    padding-top: 10px;
  }
  a.button {
    border-radius: 2px;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
    transition: all 0.5s ease 0s;
  	&:hover {
    	box-shadow: 0 2px 3px 0 rgba(0, 0, 0, 0.16), 0 2px 5px 0 rgba(0, 0, 0, 0.12);
    	transition: all 0.5s ease 0s;
    }
  }
}



.testimonial-wrap {
h2{
  margin-top: 60px;
  font-weight: 200;
  color: #333;
  text-transform: uppercase;
  margin-bottom: 15px;
  font-size: 1.5rem;
}


.centered-text {
  text-align: center;
}

.quote {
  background: #ebf3f5;
  padding: 15px 20px 5px 15px;
  border-radius: 5px;
  margin-bottom: 30px;
  &:after {
    content: '';
    width: 0px;
    height: 0px;
    border-style: solid;
    border-width: 20px 18px 0 18px;
    border-color: #ebf3f5 transparent transparent transparent;
    position: relative;
    top: 37px;
    left: 20px;
  }
}

.student {
  margin-left: 25px;
  margin-bottom: 80px;
  .photo{
    background-color: #ccc;
    border-radius: 100px;
    width: 60px;
    height: 60px;
    float: left;
    margin-right: 10px;
  }
  p{
    position: relative;
    top: 5px;
    text-transform: uppercase;
    &:nth-child(2) {
      font-size: 16px;
      font-weight: 600;
      margin-bottom: 0;
    }
    &:nth-child(3) {
      font-size: 14px;
      color: #777;
    }
  }
}
}
.infobox-header {
	background: #f1f1f1;
    color: #555;
    text-transform: uppercase;
    font-size: 1.25rem;
    font-weight: 100;
    padding: 7px 10px 6px;
    margin-bottom: 0;
}
.profile-name {
	background: #fff;
    border-bottom: 1px solid #ddd;
    padding: 10px 20px;
    h1 {
    	color: #333;
    	.age {
    		color: #666;
    	}
    	.addr {
    		display: block;
    		font-size: 18px;
    		color: #666;
    	}
    }
}
.profile-related-info {
	padding: 5px 20px;
    background: #fafafa;
    border-bottom: 1px solid #eee;
    margin-bottom: 25px;
}
.b-infobox-addr {
	background: #fbfbfb;
	margin-top: 25px;
	div {
		padding: 10px;
		border-bottom: 1px solid #eee;
		display: none;
		&:last-child {
			border-bottom: 0;
		}
		.address {margin-right: 15px;}
		.phones {
			span {
				white-space: nowrap;
				&:after {
					content: '';
					display: inline-block;
					width: 4px;
					height: 4px;
					background: #ccc;
					margin: 0 10px;
					vertical-align: middle;
				}
				&:last-child{&:after {display: none;}}
				}
		}
		&:nth-child(-n+6) {
		display: block; 
	}
	}
	.show-all {padding: 10px;margin: 0;}
	
}
.b-profile-main {
	box-shadow: 0px 0 4px rgba(0,0,0,0.2);
	background: #fff;
	margin-bottom: 35px;
}
.b-infobox {
	padding: 0 10px;
}
.mentions-title {
	background: #00B8E4;
    color: #fff;
    font-size: 1.75rem;
    font-weight: 100;
    padding: 7px 10px 6px;
    margin-bottom: 0;
}
.b-data-offer {
	font-size: .7rem;
	color: #999;
	margin-top: 50px;
}

.b-profile-mentions {
	margin-bottom: 25px;
	.group {
		margin-top: 20px;
		box-shadow: 0px 0 4px rgba(0,0,0,0.2);
    	background: #fff;
    	.group-title {
		    font-size: 1.75rem;
		    font-weight: 100;
		    padding: 7px 10px 6px;
		    background: #FAFAFA;
		    border-bottom: 1px solid #ccc;
		    margin-bottom: 0;
		    cursor: pointer;
		    i {
		    	float: right;
		    	color: #999;
			    font-size: 1rem;
			    line-height: 2.5;
		    }
    	}
    	.group-wrapper {
    		padding: 0 15px 0.1px;
    	}
    	.group-block-header {
    		background: #f1f1f1;
		    color: #555;
		    text-transform: uppercase;
		    font-size: 1.25rem;
		    font-weight: 100;
		    padding: 7px 10px 6px;
		    margin: 20px 0 15px;
    	}
    	.group-block-header-item {
    		font-size: 1.3rem;
    		line-height: 1.1;
    		color: #333;
    		margin-bottom: 6px;
    		span {color: #999;}
    	}
    	div.row {margin-top: 15px;}
    	p.row {
    		margin-bottom: 2px;
    		word-wrap: break-word;
    		
    		}
    	table {margin: 15px 0 15px;min-width: 100%;}
	}
}

.i-block {margin-bottom:15px;display:block;}  


// @include breakpoint(small only || down)
// @include breakpoint(landscape)
// @include breakpoint(retina)
@include breakpoint(small down) {
	.row {padding-left: 0px; padding-right: 0px;} 
	.row .row {padding-left: 0;padding-right: 0;}
}
@include breakpoint(medium down) {
	.row {padding-left: 10px; padding-right: 10px;}
	.row .row {padding-left: 0;padding-right: 0;}
}
@include breakpoint(large down) {
	.row {padding-left: 30px; padding-right: 30px;}
	.row .row {padding-left: 0;padding-right: 0;}
}